*{
  margin: 0;
  padding: 0px;
  box-sizing: border-box;
}
body{
  background-color: #15131a;
}

.a:hover{
  transform: translate(2px,-8px);
  transition:transform 1s;
}
.aboutmecontainer{
  border: 1px solid rgb(57, 51, 51);
  border-radius: 20px;
}

.aboutmecontainer:hover{
    border: 1px solid red;
    transform: translate(2px,-8px);
    transition: all 1s;
}